.banner {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

.video {
  object-fit: cover;
  width: 100%;
  height: 99vh;
}

.banner_content {
  position: absolute;
  top: 0;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.banner_title {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;
}

.banner_subtitle {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 64px;
  line-height: 75px;
  text-transform: uppercase;
}
