footer {
  background-color: #000;
  position: absolute;
  z-index: 2;
  bottom: 0;
  width: 100%;
}

.wrapper {
  width: 95%;
  margin: 0 auto;
}

.footer_content {
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.team {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
}

.links_items {
  display: flex;
  font-size: 22px;
}

.links_items li:not(first-child) {
  padding-left: 20px;
}

svg {
  color: #fff;
}
