.menu {
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: #000;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 0 auto;
}

.menu_logo img {
  height: 50px;
}

.menu_items {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.menu_items li:not(first-child) {
  padding-left: 20px;
}

.menu_item,
.menu_item a {
  color: #fff;
  cursor: pointer;
}
